.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

canvas {
  width: 100vw;
  height: 100vh;
  /* cursor: grab; */
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

* {
  box-sizing: border-box;
}

body {
  overflow: hidden;
}

h1 {
  display: block;
  font-size: 2em;
  margin-block-start: 0em;
  margin-block-end: 0em;

}

p {
  margin-block-start: 0em;
  margin-block-end: 0em;

}

.Preloader { 
   position: fixed;
   left: 0;
   top: 0;
   width: 100vw;
   height: 100vh;
   display: flex;
   align-items: center;
   justify-content: center;
   flex-direction: column;
   gap: 4px;
   z-index: 100;
   background: black;
}

.Preloader img {
  width: 420px;
  object-fit: contain;
}

.progress-container {
  position: relative;
  width: 360px;
  height: 2px;
  border-radius: 30px;
  background: rgb(43, 43, 43);
}

.progress-bar {
   position: absolute;
   left: 0;
   top: 0;
   border-radius: inherit;
   height: 100%;
   background: white;
   transition: all 0.4s ease;
}

.Preloader h1 {
  color: white;
  cursor: pointer;
}

.camera-reset-btn {
   display: grid;
   place-items: center;
   color: rgb(247, 247, 247);
   font-size: 40px;
   width: 65px;
   height: 65px;
   position: absolute;
   bottom: -100px;
   left: 50%;
   transform: translate(-50%,0);
   background: rgb(23,23,23);
   background: -moz-linear-gradient(332deg, rgba(23,23,23,1) 0%, rgba(0,0,0,1) 100%);
   background: -webkit-linear-gradient(332deg, rgba(23,23,23,1) 0%, rgba(0,0,0,1) 100%);
   background: linear-gradient(332deg, rgba(23,23,23,1) 0%, rgba(0,0,0,1) 100%);
   filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#171717",endColorstr="#000000",GradientType=1);
   border-radius: 50%;
   outline: 3px solid #0E0E0E;
   outline-offset: 3px;
   z-index: 30;
   cursor: pointer;
   transition: all 0.65s ease;
}


.active-camera-reset-btn {
  bottom: 40px;
}

.elem {

   cursor: pointer;
}

.character-box {
  scale: 1;
  width: 240px;
  min-height: 250px;
  border-radius: 9px; 
  background: linear-gradient(174.04deg, #FF265C 4.72%, #6D1A2F 100.02%);
      color: white;
  outline: 2px solid #FF265C; 
  outline-offset: 4px;
  display: grid;
  place-items: center;
  transition: all 0.5s ease;
  padding: 20px 12px;
  padding-top: 22px;
}


.character-box-body {
   width: 100%;
   height: 100%;
   display: flex;
   align-items: center;
   gap: 8px;
   flex-direction: column;
}

.character-box-body h1 {
   font-size: 27px;
}

.character-box-body p {
   font-size: 12px;
   line-height: 1.48;
   letter-spacing: 0.25px;
}

.character-box img {
   width: 85px;
   height: 85px;
   object-fit: cover;
   border-radius: 50%;
   position: absolute;
   left: 50%;
   transform: translate(-50%,-75%);
   top: 0;
   filter: drop-shadow(0 3px 6px rgba(0,0,0,0.6))
}

.active-element  {
    opacity: 1 !important;
    pointer-events: all;
}

.element {
  opacity: 0;
   pointer-events: none;
}

.embeded-screen {
  border-radius: 50px !important;
  overflow: hidden;
  transition: all 0.5s ease;
}


.Preloader video {
   height: 90vh;
   object-fit: contain;
}

.youtube-screen-grid {
  width: 1612px;
  height: 907px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  grid-column-gap: 35px;
  grid-row-gap: 35px;
}

.youtube-screen-grid iframe  {
  border-radius: 18px;
  border: 18px solid black;
  width: 100%;
  height: 100%;
}